@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body		{ font-family: 'NanumSquare', sans-serif; }
.normal		{ font-weight: 400 }
.bold		{ font-weight: 700 }
.bolder		{ font-weight: 800 }
.light		{ font-weight: 300 }

body {
  margin: 0;
  color: #444444;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F8F8F8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  text-align: center;
  padding-top: 100px;
  padding-bottom: 100px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.desktop-menu{background-color:#273339;position:fixed;top:0;width:100%;-webkit-transition:top 0.3s;transition:top 0.3s;padding-left:50px;z-index:999}.desktop-menu a{float:left;display:block;color:white;text-align:center;padding:15px;text-decoration:none;font-weight:bold;font-size:20px}.desktop-menu a:hover{background-color:#F8F8F8;color:#273339}

.who{margin-bottom:50px}.who img{width:250px}.who .my-info{padding-top:50px;text-align:left;padding-left:50px;font-size:20px}.who .my-info .nickname{font-size:30px;font-weight:bold}.who .my-info .intro{color:#F1A2A6;padding-top:10px;font-size:15px}.who .url-card{background-color:#273339;color:#F3F7F6;font-size:20px;border-radius:10px;display:inline-block;text-decoration:none;padding:10px 20px;margin:20px 10px 0 0}.who .url-card:hover{background-color:#F1A2A6}.who .legacy-stack-text{padding-top:40px;font-size:20px;font-weight:bold}.who .legacy-stack-exof{text-decoration:none;display:inline-block;color:white;background-color:#3889ED;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-sqlite{text-decoration:none;display:inline-block;color:white;background-color:#3B9C8F;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-etree{text-decoration:none;display:inline-block;color:white;background-color:#E93B35;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-jwt{text-decoration:none;display:inline-block;color:white;background-color:#122736;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-queue{text-decoration:none;display:inline-block;color:white;background-color:#783C3D;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-cell{text-decoration:none;display:inline-block;color:white;background-color:gray;width:100%;padding:10px 0;margin-top:5px}.who .legacy-stack-next{text-decoration:none;display:inline-block;color:white;background-color:#000;width:100%;padding:10px 0;margin-top:5px}

.contents-banner{text-align:left;position:relative;margin-bottom:10px}.contents-banner a{text-decoration:none}.contents-banner a .banner{color:var(--color);-webkit-transition:0.25s;transition:0.25s;background:none;border:1px solid;font:inherit;line-height:1;margin:0.5em;padding:2em 3em}.contents-banner a .banner:hover,.contents-banner a .banner:focus{border-color:var(--hover);color:#fff}.contents-banner a .offset{--color: #515151;--hover: #1973bc}.contents-banner a .offset{box-shadow:0.3em 0.3em 0 0 var(--color),inset 0.3em 0.3em 0 0 var(--color)}.contents-banner a .offset:hover,.contents-banner a .offset:focus{box-shadow:0 0 0 0 var(--hover),inset 6em 9em 0 0 var(--hover)}.contents-banner a .subject{font-weight:bold;font-size:25px}.contents-banner a .description{font-size:16px;margin-top:8px}.contents-banner a .date{font-size:15px;margin-top:5px;text-align:right}

.page-number-back{padding-top:40px}.page-number-back .page-number{font-size:20px;padding:10px 10px;color:#282c34;font-weight:bold}

.contents{text-align:right;font-size:16px;color:black}.contents a{font-size:18px}.contents a:hover{cursor:pointer}.contents .date{font-size:15px;margin-top:5px}

