.contents-banner {
  text-align: left;
  position: relative;
  margin-bottom: 10px;

  a {
    text-decoration: none;

    .banner {
      color: var(--color);
      -webkit-transition: 0.25s;
      transition: 0.25s;
      background: none;
      border: 1px solid;
      font: inherit;
      line-height: 1;
      margin: 0.5em;
      padding: 2em 3em;
    }
    .banner:hover, .banner:focus {
      border-color: var(--hover);
      color: #fff;
    }

    .offset {
      --color: #515151;
      --hover: #1973bc;
    }
    .offset {
      box-shadow: 0.3em 0.3em 0 0 var(--color), inset 0.3em 0.3em 0 0 var(--color);
    }
    .offset:hover, .offset:focus {
      box-shadow: 0 0 0 0 var(--hover), inset 6em 9em 0 0 var(--hover);
    }
    .subject {
      font-weight: bold;
      font-size: 25px;
    }

    .description {
      font-size: 16px;
      margin-top: 8px;
    }

    .date {
      font-size: 15px;
      margin-top: 5px;
      text-align: right;
    }
  }
}